import React from 'react';
import { useState, useEffect } from "react";
import millify from "millify";
import { Link } from "react-router-dom";
import { Card, Row, Col, Input } from "antd";


import { useGetCryptosQuery } from "../services/cryptoApi";


type CryptoCurrenciesProps={

  simplified?: boolean;

}

const CryptoCurrencies = ({simplified}: CryptoCurrenciesProps) => {


  const count = simplified ? 10 : 100;

  const { data: cryptosList, isFetching } = useGetCryptosQuery(count);

  const [cryptos, setCryptos ] = useState([]);
  const [searchTerm, setSearchTerm] = useState("")


  useEffect(()=>{

    const filteredData = cryptosList?.data?.coins.filter((coin: any) => coin.name.toLowerCase().includes(searchTerm.toLowerCase()));

    setCryptos(filteredData);

  },[cryptosList, searchTerm])




  if(isFetching) return (
    <div>
      "Loading..."
    </div>
    )


  return (
    <div>
      {!simplified && (
      <div className='search-crypto'>
        <Input placeholder='Search Cryptocurrency' onChange={(e)=> setSearchTerm(e.target.value)} />
      </div>
      )}

      <Row gutter={[32, 32]} className="crypto-card-container" >
        {cryptos?.map((currency: any)=> (
          <Col xs={24} sm={12} lg={6} className="crypto-card" key={currency.uuid}>
            <Link to={currency.coinrankingUrl} target="_blank">
                <Card title={`${currency.rank}. ${currency.name}`}
                extra={ <img className='crypto-image' src={currency.iconUrl} alt=""/>} 
                hoverable>
                    <p>Price: {millify(currency.price)} </p>
                    <p>Market Cap: {millify(currency.marketCap)}</p>
                    <p>Daily Change: {millify(currency.change)}%</p>
                </Card>
            </Link>
          </Col>

        ))}
      </Row>
    </div>
  )
}

export default CryptoCurrencies